export const baseURL =  "https://vcast.sivilization.com:1335"

// 生產總覽
const Request = axios.create({
    baseURL: baseURL
});

// 生產總覽-工單生產狀態
export const ApiGetOrderList = data => Request.get("/order/list", { params: data });
export const ApiGetArborList = data => Request.get("/arbor/list", { params: data });
